// adding style scroll to top button 
.scroll-to-top {
    position: sticky;
    top: 88vh;
    cursor: pointer;
    width: 40px;
    height: auto;
    margin-left: 2rem;
    z-index: 9999;
}
 


 // styling footer area

.menu-footer-container{
   
    ul{
        display: flex;
        list-style: none;
        justify-content: flex-end;
        align-items: center;
        a{
            padding: .5rem 1rem;
            font-weight: 600;
        }
    }
}
.copyright-area{
    display: flex;
    align-items: center;
    justify-content: center;
    padding:.5rem;
    p{
        color: #fff;
    }
}
.footer-menu-right{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0px;
  
    ul{
        display: flex;
        list-style: none; 
        margin-top: 11px;
    }
    a{
        padding: .5rem 1rem;
        color: #fff;
        &:hover{
            text-decoration: none;
            color: #c2c2c2;
        }
    }
}
.copyright{
    font-weight: 550;
    display: flex;
    justify-content: center;
}


.mr-2{
   color:$secondory-color;
   font-weight: 700;
   text-align: center;
   text-transform: capitalize;
   a{
    color: $menu-color;
    font-weight: 700;
   }
   }
   
   .entry-content p {
    color: $secondory-color;
   }
   .copyright{
    margin-bottom: 0px;
   }

   #scl{
    vertical-align: middle;
    border-style: none;
    background: #ffffff;
    border: solid 2px #712af9;
    border-radius: 50%;
    z-index: 999;
    padding: 0.5rem;
    width: 2rem;
    height: auto;
    position: relative;
    bottom: 4.4rem;
    left: 1rem;
}

#menu-footer-menu{
    list-style: none;
}

 #footer-widgets{
    h2{
        font-weight: 600;
    }
    .col-md-4{
        padding: 1rem;     
    }
 }
   
//  adding margin when on large grid images in smaller screens
@media only screen and(max-width:775px){
    #scl {
       bottom: 1.4rem;
    }
}

.footer-content{
   background: #712af9;
   margin-top: -32px;
}
